<template>
  <div class="bigBox">
    <van-list  
        v-model="loading"
        :finished="finished"
        :finished-text="finishedText"
        @load="Lazy_loading"
        :offset='100'
       >
       
      <van-nav-bar title="购物车" fixed :right-text="isSubDel?'管理':'完成'" left-arrow @click-left="onClickLeft" @click-right="isSubDel=!isSubDel" />

    <div class="emptyContent" v-if="products.length<=0">
      <van-empty description="购物车空空如也，快去购物吧。" />
      <div class="goShopping" @click="goShopping"><i class="fa fa-arrow-right" aria-hidden="true"></i>去购物</div>
    </div>
    <div class="solidContent" v-else>
    <div class="list">
        <van-swipe-cell class="list-items" v-for="(item,index) in products" :key="index">
          <div class="list-item clearfix">
            <!-- 单选框 -->
            <div class="fl checkbox-item" >
              <van-checkbox @change="MultipleChoice(item)" v-model="item.isActive" shape="round" />
            </div>
            <!-- 商品信息 -->
            <div class="fl product-info clearfix">
              <div class="product-img fl">
                  <img :src="item.small_img">
              </div>
              <div class="fl product-content">
                <div class="product-text">
                  <div class="clearfix product-name">
                    <span class="product-title fl">{{item.name}}</span>
                    <span class="fl product-rule">{{item.rule}}</span>
                  </div>
                  <div class="product-enname">{{item.enname}}</div>
                </div>

                <div class="product-price">
                  <div class="price fl">￥{{item.price}}</div>
                  <div class="fr product-count">
                    <van-stepper
                      v-model="item.count"
                      :integer="true"
                      theme="round"
                      button-size="22"
                      disable-input
                      @change="modifyCount(item)"
                    />
                  </div>
                </div>

              </div>
            </div>
          </div>
          <template #right>
            <van-button square type="danger" text="删除" @click="removeShopcart([item.sid], index)" />
          </template>
        </van-swipe-cell>
    </div>

      <!-- btn -->
      <van-submit-bar :price="click_computed" button-text="提交订单" :disabled="isAbled" v-if="isSubDel" @submit="sumbitOrder">
        <van-checkbox v-model="Allchecked" @click="AllSelect"  >全选</van-checkbox>
      </van-submit-bar>

      <van-submit-bar  class="delBox" button-text="删除选择" :disabled="isAbled" v-else @submit="removeMoreShopcart">
        <van-checkbox v-model="Allchecked" @click="AllSelect"  >全选</van-checkbox>
      </van-submit-bar>
    </div>
    </van-list>  
    <tabbar></tabbar>
  </div>
</template>

<script>
import tabbar from '../../components/tabbar'
import "../../assets/less/shopcart.less";
export default {
  data() {
    return {
        Allchecked: false,//全选框
        ishas: false, //购物车有无商品
        products:[],
        Allproducts:[],
        loading: true,
        finished: false,
        finishedText:'没有更多了',
        loadCount:8,
        isAbled:true,//提交按钮和删除按钮的禁用与否
        isSubDel:true,//切换管理
        
    };
  },
  components:{
    tabbar
  },
  created(){
      this.findProductNumber()
    //判断
    if(!localStorage.getItem('__Tk')){
      localStorage.setItem('isloginCbg','')
    }
  },
  methods: {
    //返回上一级
    onClickLeft() {
      this.$router.go(-1);
    },

    //查询购物车商品数量
    findProductNumber(){

        //获取token
      let tokenString = localStorage.getItem('__Tk');

        //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      return this.axios({
          method:'GET',
          url:'/findAllShopcart',
          params:{
            appkey:this.appkey,
            tokenString
          }
      }).then(result => {
            
            this.$toast.clear()
            if(result.data.code == 5000){
                result.data.result.forEach(e => {
                    e.isActive = false
                });

                this.Allproducts = result.data.result;
                
                this.products  = this.Allproducts.splice(0, this.loadCount);
                
                if(this.Allproducts.length>0){
                    this.loading = false;
                    
                }else{
                    this.loading = true;
                    this.finished = true;
                }

                if(this.products.length == 0){
                    this.finishedText = ''
                }
            }
            
        }).catch(() => {
            
            this.$toast.clear()
        })
    },

    //懒加载
    Lazy_loading(){

      
        
        
        // 
        
        setTimeout(() => {
        
        
        if (this.Allproducts.length <= 0) {
            
            
          // //没有数据可加载
          // this.loading = true;
          // this.finished = true;
        }else if(this.Allproducts.length > 0||this.Allproducts.length<8){
          this.products.push(...this.Allproducts.splice(0, this.Allproducts.length));
          this.loading = true;
          this.finished = true;
        }else{
          this.products.push(...this.Allproducts.splice(0, this.loadCount));
        }
        
        
        
        
        }, 1000)  
    },

    //全选
    AllSelect(){
        
        this.products.forEach(e => {
             e.isActive = this.Allchecked
        })
        this.Allproducts.forEach(e => {
        e.isActive = this.Allchecked
        })
        this.isAbled = !this.Allchecked
    },

    //单选

    MultipleChoice(){
      
      
      this.isAbled = true;
      
      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].isActive) {
          this.isAbled = false;
          break;
        }
      }

      for (let i = 0; i < this.products.length; i++) {
        if (!this.products[i].isActive) {
          
          
          this.Allchecked = false;
          return;
        }
      }

      this.Allchecked = true;
      
    },

    //修改购物车数量
    modifyCount(item) {

      let tokenString = localStorage.getItem('__Tk');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'POST',
        url: '/modifyShopcartCount',
        data: {
          appkey: this.appkey,
          tokenString,
          sid: item.sid,
          count: item.count
        }
      }).then(() => {
        this.$toast.clear();
        

      }).catch(() => {
        this.$toast.clear();
        
      })
    },

    //单个删除
    removeShopcart(sids, index) {

      //获取购物车的sid
      let tokenString = localStorage.getItem('__Tk');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'POST',
        url: '/removeShopcart',
        data: {
          appkey: this.appkey,
          tokenString,

          //sid购物车唯一标识集合字符串
          sids: JSON.stringify(sids)
        }
      }).then(result => {
        this.$toast.clear();

        if (result.data.code == 7000) {
          this.products.splice(index, 1);
          if (this.products.length == 0) {
            this.finishedText = '';
          }
          
          this.$toast.success('删除成功');
        } else {
          this.$toast.success('删除失败');
        }
        
      }).catch(() => {
        this.$toast.clear();
        
      })
    },

    //多个删除
    removeMoreShopcart() {
      
      
      //获取删除购物车sid
      let sids = [];
      this.products.forEach(e => {
        if (e.isActive) {
          sids.push(e.sid);
        }
      })

      
      //获取购物车的sid
      let tokenString = localStorage.getItem('__Tk');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'POST',
        url: '/removeShopcart',
        data: {
          appkey: this.appkey,
          tokenString,

          //sid购物车唯一标识集合字符串
          sids: JSON.stringify(sids)
        }
      }).then(result => {
        this.$toast.clear();

        if (result.data.code == 7000) {


           //删除页面的购物车商品
          for (let i = this.products.length - 1; i >= 0; i--) {
            if (this.products[i].isActive) {
              this.products.splice(i, 1);
            }
          }

          if (this.products.length == 0) {
            this.finishedText = '';
          }
          
          this.$toast.success('删除成功');
        } else {
          this.$toast.success('删除失败');
        }
        
      }).catch(() => {
        this.$toast.clear();
        
      })
    },

    //跳转购物页面
    goShopping(){
      this.$router.push({name:'menu'})
    },

    //提交订单
  sumbitOrder(){
    let sids = []
    this.products.forEach(e => {
      if(e.isActive){
        sids.push(e.sid)
      }
    })
    
    this.$router.push({name:'clearing',query:{sids:sids.join('-')}})
    
  }

  },
  computed:{
    click_computed(){
      let aa = 0;

      for (let i = 0; i < this.products.length; i++) {
        if (this.products[i].isActive) {
          aa+= this.products[i].count*this.products[i].price*100
        }
      }
      return aa
    }
  },
  
};
</script>

<style lang="less" scoped>
</style>